body {
  background-color: #1e53de;
}

.app__form {
  width: 742px;
  height: 492px;
  margin-left: auto;
  margin-right: auto;
  background: white;
  margin-top: 94px;
}

.head {
  width: 384px;
  height: 42px;
  top: 113px;

  margin-left: 1px;
}

.small {
  width: 357px;
  margin-left: 1px;
  margin-top: -40px;
  margin-bottom: 6px;
  color: #958383;
}

.weekDays-selector {
  margin-bottom: 10px;
}

.weekDays-selector input {
  display: none !important;
}

.weekDays-selector input[type="checkbox"] + label {
  display: inline-block;
  border-radius: 6px;
  background: #dddddd;
  height: 33px;
  width: 30px;
  margin-right: 3px;
  line-height: 40px;
  text-align: center;
  cursor: pointer;
  margin-left: 4px;
}

.weekDays-selector input[type="checkbox"]:checked + label {
  background: blue;
  color: #ffffff;
}

.react-datepicker-wrapper {
  display: inline-block;
  padding: 0;
  border: 0;
  margin-left: 231px;
}

.multiselect {
  position: relative;
  text-align: left;
  width: 19%;
  height: 0px;
  margin-left: 0;
  bottom: -9px;
  margin-bottom: 39px;
}
